import React, { useEffect } from 'react';
import Filters from '../filters/Filters';
import axios from 'axios';
import './Search.scss';

const Search = ({
    setResults,
    setMessage,
    filters,
    setFilters,
    currentPage,
    totalResults,
    setTotalResults,
    setLoader
}) => {
    useEffect(() => {
        search();
    }, []);

    const search = () => {
        console.log(filters.test.myname);
        setLoader(true);
        let url = `http://localhost:3001/${currentPage ? currentPage : ''}?`;
        for (const key in filters) {
            if (filters[key].length) {
                url += `${key}=${filters[key]}&`;
            }
        }
        axios.get(url).then((res) => {
            if (!res.data.error) {
                setResults(res.data.articles);
                setTotalResults(res.data.totalResults);
                res.data.articles.length
                    ? setMessage(
                          `Showing ${res.data.articles.length} of ${res.data.totalResults}`
                      )
                    : setMessage(
                          'No search results found, please search with different parameters'
                      );
                setLoader(false);
                return;
            } else {
                if (res.data.code === 'missingParams') {
                    setMessage(res.data.message);
                }
                setResults([]);
                setLoader(false);
                return;
            }
        });
    };

    const handleSearch = (e) => {
        e.preventDefault();
        search();
    };
    return (
        <form onSubmit={(e) => handleSearch(e)}>
            <Filters
                filters={filters}
                setFilters={setFilters}
                currentPage={currentPage}
            />
            <div className='load-more-container'>
                <button>GET NEWS</button>
            </div>
        </form>
    );
};

export default Search;
