import React from 'react';
import axios from 'axios';

import './Results.scss';

const Results = ({
    results,
    setResults,
    setMessage,
    filters,
    setFilters,
    currentPage,
    totalResults,
    setTotalResults,
    setLoader
}) => {
    const loadMore = (e) => {
        const newFilters = filters;
        newFilters.page++;
        setFilters(newFilters);
        searchMore();
    };

    const searchMore = () => {
        setLoader(true);
        let url = `http://localhost:3001/${currentPage ? currentPage : ''}?`;
        for (const key in filters) {
            if (filters[key].length) {
                url += `${key}=${filters[key]}&`;
            }
        }
        axios.get(url).then((res) => {
            if (!res.data.error) {
                const moreResults = [...results, ...res.data.articles];
                setResults(moreResults);
                setTotalResults(res.data.totalResults);
                res.data.articles.length
                    ? setMessage(
                          `Showing ${moreResults.length} of ${res.data.totalResults}`
                      )
                    : setMessage(
                          'No search results found, please search with different parameters'
                      );
                setLoader(false);
                return;
            } else {
                if (res.data.code === 'missingParams') {
                    setMessage(res.data.message);
                }
                setResults([]);
                setLoader(false);
                return;
            }
        });
    };
    return (
        <>
            <ul>
                {results.map((article, i) => {
                    return (
                        <li className='results-list' key={`atricle-${i}`}>
                            <div className='article-preview'>
                                <div className='title-container'>
                                    <p className='article-title'>
                                        <b>{article.title}</b>
                                    </p>
                                </div>
                                <div className='right-content-container'>
                                    {article.urlToImage ? (
                                        <img
                                            className='thumbnail'
                                            src={article.urlToImage}
                                            alt=''
                                        />
                                    ) : null}
                                    <p className='published-date'>
                                        {article.publishedAt
                                            ? new Date(
                                                  article.publishedAt
                                              ).toUTCString()
                                            : ''}
                                    </p>
                                </div>
                            </div>
                            <div className='full-article'>
                                <p className='article-description'>
                                    {article.description
                                        ? article.description
                                              .replace(/&quot;/g, '"')
                                              .replace(/&amp;/g, '&')
                                              .replace(/<\/?[^>]+(>|$)/g, '')
                                        : ''}
                                </p>

                                {article.content ? (
                                    <p className='article-content'>
                                        {article.content
                                            .slice(
                                                0,
                                                article.content.indexOf('[+')
                                            )
                                            .replace(/&quot;/g, '"')
                                            .replace(/&amp;/g, '&')
                                            .replace(/<\/?[^>]+(>|$)/g, '')}
                                    </p>
                                ) : (
                                    ''
                                )}
                                <div className='bottom'>
                                    <a
                                        rel='noopener noreferrer'
                                        href={article.url}
                                        target='_blank'
                                        className='article-link'
                                    >
                                        [+ more]
                                    </a>
                                    <p className='author-name'>
                                        {article.source?.name
                                            ? ` -  ${article.author}`
                                            : ``}
                                    </p>
                                </div>
                            </div>
                        </li>
                    );
                })}
            </ul>
            {parseInt(totalResults) -
                parseInt(filters.pageSize * filters.page) <=
            0 ? null : (
                <div className='load-more-container'>
                    <button onClick={(e) => loadMore(e)}>LOAD MORE</button>
                </div>
            )}
        </>
    );
};

export default Results;
